export default {
  name: '',
  props: {},
  data: function() {
    // 資料
    return {
      reportItems: {
        // 改為載入顯示昨日日期，「今天」為今日日期，「昨天」為昨日日期，其他按鈕依照正確時間顯示日期
        countDate: '', // 起始時間
        countDateEnd: '' // 結束時間
      },
      // 日期選擇器
      flatpickrDate: {
        startDate: '',
        endDate: ''
      },
      // 當前已點擊的日期按鈕 (Today/ Yesterday/ LastWeek/ ThisWeek/ LastMonth/ ThisMonth)
      currentClickDateBtn: '',
      // 當前快選按鈕日期
      dateBtnItems: {
        Today: {
          startDate: '',
          endDate: ''
        },
        Yesterday: {
          startDate: '',
          endDate: ''
        },
        ThisWeek: {
          startDate: '',
          endDate: ''
        },
        LastWeek: {
          startDate: '',
          endDate: ''
        },
        ThisMonth: {
          startDate: '',
          endDate: ''
        },
        LastMonth: {
          startDate: '',
          endDate: ''
        }
      },
      // 載入當日注單最小帳務日狀態
      getCurrentBillsDateStatus: false,
      // 日期選擇器設定
      flatpickrConfig: {
        locale: {
          weekdays: {}, // *initLanguage*
          // weekdays: {
          //   shorthand: ['週日', '週一', '週二', '週三', '週四', '週五', '週六'],
          //   longhand: ['星期日','星期一','星期二','星期三','星期四','星期五','星期六']
          // },
          months: {} // *initLanguage*
          // months: {
          //   shorthand: ['一月','二月','三月','四月','五月','六月','七月','八月','九月','十月','十一月','十二月'],
          //   longhand: ['一月','二月','三月','四月','五月','六月','七月','八月','九月','十月','十一月','十二月']
          // }
        },
        dateFormat: 'Y-m-d',
        enableTime: false,
        time_24hr: true
      }
    };
  },
  watch: {
    // '$store.state.lang' (value) {
    //   this.initLanguage()
    //
    //   /* 初始日期選擇器設定 */
    //   // 開始日期
    //   this.flatpickrDate.startDate = flatpickr('[name="start-date"].date-picker', _merge(this.flatpickrConfig)).setDate(this.reportItems.countDate)
    //   // 結束日期
    //   this.flatpickrDate.endDate = flatpickr('[name="end-date"].date-picker', _merge(this.flatpickrConfig)).setDate(this.reportItems.countDateEnd)
    // },
    //監聽值
    'getCurrentBillsDateStatus' (status) {
      if (status && this.$route.path.search('history-report') !== -1) {
        // 歷史報表，若載入日期與按鈕日期有一樣，則預設快選按鈕顏色
        for (let dateType in this.dateBtnItems) {
          if (this.reportItems.countDate === this.dateBtnItems[dateType].startDate &&
              this.reportItems.countDateEnd === this.dateBtnItems[dateType].endDate) {
            this.currentClickDateBtn = dateType
          }
        }
      }
    }
  },
  computed: {
    //相依的資料改變時才做計算方法
  },
  methods: {
    // 初始日期及日期選擇器
    initDate () {
      // 初始化日期選擇器(開始日期)
      this.flatpickrDate.startDate = flatpickr(
        '[name="start-date"].date-picker',
        _merge(this.flatpickrConfig, {
          defaultDate: moment()
            .add(-1, "days")
            .format("YYYY-MM-DD")
        })
      );
      // 初始化日期選擇器(結束日期)
      this.flatpickrDate.endDate = flatpickr(
        '[name="end-date"].date-picker',
        _merge(this.flatpickrConfig, {
          defaultDate: moment()
            .add(-1, "days")
            .format("YYYY-MM-DD")
        })
      );

      let newDate = new Date();
      var date =
        newDate.getFullYear() +
        "-" +
        (newDate.getMonth() + 1 < 10 ? "0" : "") +
        (newDate.getMonth() + 1) +
        "-" +
        (newDate.getDate() < 10 ? "0" : "") +
        newDate.getDate();
      this.reportItems.countDate = date;
      this.reportItems.countDateEnd = date;
    },
    // 初始快選按鈕日期
    initDateBtn () {
      let dateToday = date.setToday()
      let dateYesterday = date.setYesterday()
      let dateThisWeek = date.setThisWeek()
      let dateLastWeek = date.setLastWeek()
      let dateThisMonth = date.setThisMonth()
      let dateLastMonth = date.setLastMonth()
      // 「今天」
      this.dateBtnItems.Today.startDate = dateToday.Start
      this.dateBtnItems.Today.endDate = dateToday.End
      // 「昨天」
      this.dateBtnItems.Yesterday.startDate = dateYesterday.Start
      this.dateBtnItems.Yesterday.endDate = dateYesterday.End
      // 「本週」
      this.dateBtnItems.ThisWeek.startDate = dateThisWeek.Start
      this.dateBtnItems.ThisWeek.endDate = dateThisWeek.End
      // 「上週」
      this.dateBtnItems.LastWeek.startDate = dateLastWeek.Start
      this.dateBtnItems.LastWeek.endDate = dateLastWeek.End
      // 「本月」
      this.dateBtnItems.ThisMonth.startDate = dateThisMonth.Start
      this.dateBtnItems.ThisMonth.endDate = dateThisMonth.End
      // 「上月」
      this.dateBtnItems.LastMonth.startDate = dateLastMonth.Start
      this.dateBtnItems.LastMonth.endDate = dateLastMonth.End

      // 帳務紀錄或額度查詢，若載入日期與按鈕日期有一樣，則預設快選按鈕顏色
      if (this.$route.path.search('billing-records') !== -1 || this.$route.path.search('amout-search') !== -1) {
        for (let dateType in this.dateBtnItems) {
          if (this.reportItems.countDate === this.dateBtnItems[dateType].startDate &&
              this.reportItems.countDateEnd === this.dateBtnItems[dateType].endDate) {
            this.currentClickDateBtn = dateType
          }
        }
      }
    },
    setToday() {
      this.reportItems.countDate = this.dateBtnItems.Today.startDate;
      this.reportItems.countDateEnd = this.dateBtnItems.Today.endDate;

      // 更新日期選擇器(開始日期)
      this.flatpickrDate.startDate.setDate(this.dateBtnItems.Today.startDate);
      // 更新日期選擇器(結束日期)
      this.flatpickrDate.endDate.setDate(this.dateBtnItems.Today.endDate);

      // 更新當前已點擊的日期按鈕
      this.currentClickDateBtn = 'Today'
    },
    setYesterday() {
      this.reportItems.countDate = this.dateBtnItems.Yesterday.startDate;
      this.reportItems.countDateEnd = this.dateBtnItems.Yesterday.endDate;

      // 更新日期選擇器(開始日期)
      this.flatpickrDate.startDate.setDate(this.dateBtnItems.Yesterday.startDate);
      // 更新日期選擇器(結束日期)
      this.flatpickrDate.endDate.setDate(this.dateBtnItems.Yesterday.endDate);

      // 更新當前已點擊的日期按鈕
      this.currentClickDateBtn = 'Yesterday'
    },
    setThisWeek() {
      this.reportItems.countDate = this.dateBtnItems.ThisWeek.startDate;
      this.reportItems.countDateEnd = this.dateBtnItems.ThisWeek.endDate;

      // 更新日期選擇器(開始日期)
      this.flatpickrDate.startDate.setDate(this.dateBtnItems.ThisWeek.startDate);
      // 更新日期選擇器(結束日期)
      this.flatpickrDate.endDate.setDate(this.dateBtnItems.ThisWeek.endDate);

      // 更新當前已點擊的日期按鈕
      this.currentClickDateBtn = 'ThisWeek'
    },
    setLastWeek() {
      this.reportItems.countDate = this.dateBtnItems.LastWeek.startDate;
      this.reportItems.countDateEnd = this.dateBtnItems.LastWeek.endDate;

      // 更新日期選擇器(開始日期)
      this.flatpickrDate.startDate.setDate(this.dateBtnItems.LastWeek.startDate);
      // 更新日期選擇器(結束日期)
      this.flatpickrDate.endDate.setDate(this.dateBtnItems.LastWeek.endDate);

      // 更新當前已點擊的日期按鈕
      this.currentClickDateBtn = 'LastWeek'
    },
    setThisMonth() {
      this.reportItems.countDate = this.dateBtnItems.ThisMonth.startDate;
      this.reportItems.countDateEnd = this.dateBtnItems.ThisMonth.endDate;

      // 更新日期選擇器(開始日期)
      this.flatpickrDate.startDate.setDate(this.dateBtnItems.ThisMonth.startDate);
      // 更新日期選擇器(結束日期)
      this.flatpickrDate.endDate.setDate(this.dateBtnItems.ThisMonth.endDate);

      // 更新當前已點擊的日期按鈕
      this.currentClickDateBtn = 'ThisMonth'
    },
    setLastMonth() {
      this.reportItems.countDate = this.dateBtnItems.LastMonth.startDate;
      this.reportItems.countDateEnd = this.dateBtnItems.LastMonth.endDate;

      // 更新日期選擇器(開始日期)
      this.flatpickrDate.startDate.setDate(this.dateBtnItems.LastMonth.startDate);
      // 更新日期選擇器(結束日期)
      this.flatpickrDate.endDate.setDate(this.dateBtnItems.LastMonth.endDate);

      // 更新當前已點擊的日期按鈕
      this.currentClickDateBtn = 'LastMonth'
    }
    // // 初始語系
    // initLanguage () {
    //   // 日期選擇器設定
    //   this.flatpickrConfig.locale.weekdays = {
    //     shorthand: [
    //       this.$ln('週日'),
    //       this.$ln('週一'),
    //       this.$ln('週二'),
    //       this.$ln('週三'),
    //       this.$ln('週四'),
    //       this.$ln('週五'),
    //       this.$ln('週六')
    //     ],
    //     longhand: [
    //       this.$ln('星期日'),
    //       this.$ln('星期一'),
    //       this.$ln('星期二'),
    //       this.$ln('星期三'),
    //       this.$ln('星期四'),
    //       this.$ln('星期五'),
    //       this.$ln('星期六')
    //     ]
    //   }
    //   this.flatpickrConfig.locale.months = {
    //     shorthand: [
    //       this.$ln('一月'),
    //       this.$ln('二月'),
    //       this.$ln('三月'),
    //       this.$ln('四月'),
    //       this.$ln('五月'),
    //       this.$ln('六月'),
    //       this.$ln('七月'),
    //       this.$ln('八月'),
    //       this.$ln('九月'),
    //       this.$ln('十月'),
    //       this.$ln('十一月'),
    //       this.$ln('十二月')
    //     ],
    //     longhand: [
    //       this.$ln('一月'),
    //       this.$ln('二月'),
    //       this.$ln('三月'),
    //       this.$ln('四月'),
    //       this.$ln('五月'),
    //       this.$ln('六月'),
    //       this.$ln('七月'),
    //       this.$ln('八月'),
    //       this.$ln('九月'),
    //       this.$ln('十月'),
    //       this.$ln('十一月'),
    //       this.$ln('十二月')
    //     ]
    //   }
    //
    //   // 查詢帳號
    //   this.searchAccountItems.rules = [
    //     v => ((/^[A-Za-z0-9]+$/.test(v)) || v === '') || this.$ln(`需為英文或數字，不能為空格`)
    //   ]
    // }
  },
  //BEGIN--生命週期
  beforeCreate: function() {
    //實體初始化
  },
  created: function() {
    //實體建立完成。資料 data 已可取得，但 el 屬性還未被建立。
  },
  beforeMount: function() {
    //執行元素掛載之前。
  },
  mounted: function() {
    //元素已掛載， el 被建立。
    // this.initLanguage()
  },
  beforeUpdate: function() {
    //當資料變化時被呼叫，還不會描繪 View。
  },
  updated: function() {
    //當資料變化時被呼叫，還不會描繪 View。
  },
  beforeDestroy: function() {
    //實體還可使用。
  },
  destroyed: function() {
    //實體銷毀。
  }
