<template>
  <div class="billingRecords">
    <v-container fluid>
      <!-- 標題列 -->
      <CommonBtnTitle
        :Title="$ln('帳務紀錄')"
        :BtnTitle="$ln('帳務查詢')"
        :BtnFunc="search"
        :BtnParams="{
          disabled:searchAccountItems.valid
        }"
      />

      <material-card>
        <v-form v-model="billingRecordItems.valid">
          <!-- 日期 -->
          <CommonDatePicker
            :Title="$ln('日期')"
            :StartTime.sync="reportItems.countDate"
            :EndTime.sync="reportItems.countDateEnd"
            :CurrentClickDateBtn.sync="currentClickDateBtn"
            :QuickyBtn="[
              { name:'今天', type: 'Today' , func: ()=>setToday() },
              { name:'昨天', type: 'Yesterday' , func: ()=>setYesterday() },
              { name:'上週', type: 'LastWeek' , func: ()=>setLastWeek() },
              { name:'本週', type: 'ThisWeek' , func: ()=>setThisWeek() },
              { name:'上月', type: 'LastMonth' , func: ()=>setLastMonth() },
              { name:'本月', type: 'ThisMonth' , func: ()=>setThisMonth() },
            ]"
          />

          <!-- 查詢對象 -->
          <v-layout row wrap>
            <v-flex xl1 md2 xs12 class="pt-3">{{ $ln('查詢對象') }}</v-flex>
            <v-flex xl9 md10 xs12>
              <v-radio-group v-model="billingRecordItems.stratumItems.selected" row>
                <v-radio
                  class="v-input-checkbox"
                  v-for="(item, i) in billingRecordItems.stratumItems.options"
                  v-show="item.show"
                  :key="i"
                  :label="$ln(item.text)"
                  :value="item.value"
                />
              </v-radio-group>
            </v-flex>
          </v-layout>
          <!-- 查詢帳號 -->
          <v-form v-model="searchAccountItems.valid">
            <v-layout row wrap>
              <v-flex xl1 md2 xs12 class="pt-3">{{ $ln('查詢帳號') }}</v-flex>
              <v-flex xl2 md3 xs12>
                <v-text-field
                  v-model="billingRecordItems.search_acc"
                  :rules="searchAccountItems.rules"
                  required
                />
              </v-flex>
            </v-layout>
          </v-form>
        </v-form>
      </material-card>

      <!-- 底部按鈕 -->
      <CommonBtnTitle
        Title=""
        :BtnTitle="$ln('帳務查詢')"
        :BtnFunc="search"
        :BtnParams="{
          disabled:searchAccountItems.valid
        }"
      />
    </v-container>
  </div>
</template>

<script>
import moment from 'moment'
import _merge from "lodash/merge";
import DateTime from "@/mixins/other/DateTime";

export default {
  name: "BillingRecords",
  mixins:[DateTime],
  data() {
    return {
      // 當前已點擊的日期按鈕 (Today/ Yesterday/ LastWeek/ ThisWeek/ LastMonth/ ThisMonth)
      currentClickDateBtn: '',
      searchAccountItems: {
        valid: false,
        account: "",
        rules: [] // *initLanguage*
        // rules: [
        //   v =>
        //     /^[A-Za-z0-9]+$/.test(v) || v === "" || "需為英文或數字，不能為空格"
        // ]
      },
      // 選擇level帳務紀錄資料
      billingRecordItems: {
        valid: false,
        stratumItems: {
          selected: 1,
          options: [
            {
              show: true,
              text: "大總監",
              value: 7
            },
            {
              show: true,
              text: "總監",
              value: 6
            },
            {
              show: true,
              text: "大股東",
              value: 5
            },
            {
              show: true,
              text: "股東",
              value: 4
            },
            {
              show: true,
              text: "總代理",
              value: 3
            },
            {
              show: true,
              text: "代理",
              value: 2
            },
            {
              show: true,
              text: "會員",
              value: 1
            }
          ]
        },
        search_acc: ""
      },
    };
  },
  watch: {
    '$store.state.lang' (value) {
      this.initLanguage()

      /* 初始日期選擇器設定 */
      // 開始日期
      this.flatpickrDate.startDate = flatpickr('[name="start-date"].date-picker', _merge(this.flatpickrConfig))
      this.flatpickrDate.startDate.setDate(this.reportItems.countDate)
      // 結束日期
      this.flatpickrDate.endDate = flatpickr('[name="end-date"].date-picker', _merge(this.flatpickrConfig))
      this.flatpickrDate.endDate.setDate(this.reportItems.countDateEnd)
    }
  },
  mounted () {
    this.initLanguage()

    // console.log(sessionStorage.getItem('super.manage.level'))
    if (
      parseInt(sessionStorage.getItem("super.manage.level")) === 7 ||
      parseInt(sessionStorage.getItem("super.manage.subLevel")) === 7
    ) {
      this.billingRecordItems.stratumItems.options[0].show = false;
    } else if (
      parseInt(sessionStorage.getItem("super.manage.level")) === 6 ||
      parseInt(sessionStorage.getItem("super.manage.subLevel")) === 6
    ) {
      this.billingRecordItems.stratumItems.options[0].show = false;
      this.billingRecordItems.stratumItems.options[1].show = false;
    } else if (
      parseInt(sessionStorage.getItem("super.manage.level")) === 5 ||
      parseInt(sessionStorage.getItem("super.manage.subLevel")) === 5
    ) {
      this.billingRecordItems.stratumItems.options[0].show = false;
      this.billingRecordItems.stratumItems.options[1].show = false;
      this.billingRecordItems.stratumItems.options[2].show = false;
    } else if (
      parseInt(sessionStorage.getItem("super.manage.level")) === 4 ||
      parseInt(sessionStorage.getItem("super.manage.subLevel")) === 4
    ) {
      this.billingRecordItems.stratumItems.options[0].show = false;
      this.billingRecordItems.stratumItems.options[1].show = false;
      this.billingRecordItems.stratumItems.options[2].show = false;
      this.billingRecordItems.stratumItems.options[3].show = false;
    } else if (
      parseInt(sessionStorage.getItem("super.manage.level")) === 3 ||
      parseInt(sessionStorage.getItem("super.manage.subLevel")) === 3
    ) {
      this.billingRecordItems.stratumItems.options[0].show = false;
      this.billingRecordItems.stratumItems.options[1].show = false;
      this.billingRecordItems.stratumItems.options[2].show = false;
      this.billingRecordItems.stratumItems.options[3].show = false;
      this.billingRecordItems.stratumItems.options[4].show = false;
    } else if (
      parseInt(sessionStorage.getItem("super.manage.level")) === 2 ||
      parseInt(sessionStorage.getItem("super.manage.subLevel")) === 2
    ) {
      this.billingRecordItems.stratumItems.options[0].show = false;
      this.billingRecordItems.stratumItems.options[1].show = false;
      this.billingRecordItems.stratumItems.options[2].show = false;
      this.billingRecordItems.stratumItems.options[3].show = false;
      this.billingRecordItems.stratumItems.options[4].show = false;
      this.billingRecordItems.stratumItems.options[5].show = false;
    }

    this.initDate()
    this.initDateBtn()
  },
  methods: {
    search () {
      this.$router.push({
        path: "/manage/report/billing-records/Result",
        query: {
          search_acc: this.billingRecordItems.search_acc,
          level: this.billingRecordItems.stratumItems.selected,
          start_time: this.reportItems.countDate,
          end_time: this.reportItems.countDateEnd
        }
      });
    },
    // 初始語系
    initLanguage () {
      // 日期選擇器設定
      this.flatpickrConfig.locale.weekdays = {
        shorthand: [
          this.$ln('週日'),
          this.$ln('週一'),
          this.$ln('週二'),
          this.$ln('週三'),
          this.$ln('週四'),
          this.$ln('週五'),
          this.$ln('週六')
        ],
        longhand: [
          this.$ln('星期日'),
          this.$ln('星期一'),
          this.$ln('星期二'),
          this.$ln('星期三'),
          this.$ln('星期四'),
          this.$ln('星期五'),
          this.$ln('星期六')
        ]
      }
      this.flatpickrConfig.locale.months = {
        shorthand: [
          this.$ln('一月'),
          this.$ln('二月'),
          this.$ln('三月'),
          this.$ln('四月'),
          this.$ln('五月'),
          this.$ln('六月'),
          this.$ln('七月'),
          this.$ln('八月'),
          this.$ln('九月'),
          this.$ln('十月'),
          this.$ln('十一月'),
          this.$ln('十二月')
        ],
        longhand: [
          this.$ln('一月'),
          this.$ln('二月'),
          this.$ln('三月'),
          this.$ln('四月'),
          this.$ln('五月'),
          this.$ln('六月'),
          this.$ln('七月'),
          this.$ln('八月'),
          this.$ln('九月'),
          this.$ln('十月'),
          this.$ln('十一月'),
          this.$ln('十二月')
        ]
      }

      // 查詢帳號
      this.searchAccountItems.rules = [
        v =>
        /^[A-Za-z0-9]+$/.test(v) || v === "" || this.$ln("需為英文或數字，不能為空格")
      ]
    }
  }
};
</script>

<style lang="scss">
</style>
