import moment from "moment";

export default {
    // 初始

    // 取前N天 ,後N天日期
    setday(count = 0, format = 'YYYY-MM-DD') {
        return moment().add(count, 'days').format(format)
    },
    // 今天
    setToday(format = 'YYYY-MM-DD') {
        return {
            Start: moment().format(format),
            End: moment().format(format)
        }
    },
    // 昨天
    setYesterday(format = 'YYYY-MM-DD') {
        return {
            Start: moment().add(-1, 'days').format(format),
            End: moment().add(-1, 'days').format(format)
        }
    },
    // 本週 正確時間
    setRealThisWeek(format = 'YYYY-MM-DD') {
        return {
            Start: moment().startOf('isoweek').format(format),
            End: moment().endOf('isoweek').format(format)
        }
    },
    // 本週 隨結帳日變化時間
    setThisWeek(format = 'YYYY-MM-DD') {
        let data = this.setRealThisWeek(format);
        if (moment().day() === 0) {
            data = {
                Start: this.setday(1),
                End: this.setday(0)
            }
        }
        return data;
    },
    // 上週 正確時間
    setRealLastWeek(format = 'YYYY-MM-DD') {
        return {
            Start: moment().week(moment().week() - 1).startOf('isoweek').format(format),
            End: moment().week(moment().week() - 1).endOf('isoweek').format(format)
        }
    },
    // 上週 隨結帳日變化時間
    setLastWeek(format = 'YYYY-MM-DD') {
        let data = this.setRealLastWeek(format);
        if (moment().day() === 0) {
            data = this.setRealThisWeek(format);
        }
        return data;
    },
    // 本月
    setThisMonth(format = 'YYYY-MM-DD') {
        return {
            Start: moment().startOf('month').format(format),
            End: moment().endOf('month').format(format)
        }
    },
    // 上月
    setLastMonth(format = 'YYYY-MM-DD') {
        return {
            Start: moment().month(moment().month() - 1).startOf('month').format(format),
            End: moment().month(moment().month() - 1).endOf('month').format(format)
        }
    }
}
